.favorites {
    position: relative;
    margin-top: 6.5rem;
}

.favoritesWrap {
    display: flex;
    justify-content: center;
}

.none {
    margin-top: 5rem;
    text-align: center;
}

.favorites a,
.favorites a:visited,
.favorites a:hover,
.favorites a:focus,
.favorites a:active {
    text-decoration: none;
    color: #FCA311;
}